.board-filter {
	display: flex;
	gap: 1px;

	.member-container {
		position: relative;

		.empty-filter-btn {
			display: flex;
			align-items: center;
			margin-inline-end: 5px;
			background-color: $clr1;
			width: 10px;
			height: 10px;
			border-radius: 50%;
			color: $clr0;
		}

		.member-filter {
			width: 200px;
			height: 200px;
			position: absolute;
			top: 34px;
			background-color: $clr0;
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			gap: 5px;
			padding: 16px;
			box-shadow: $default-box-shadow;
			border-radius: 8px;

			.member-list {
				display: flex;

				img {
					cursor: pointer;
					width: 30px;
					height: 30px;
					border-radius: 50%;
				}
			}
		}
	}

	.search-container {
		padding-inline-start: 5px;
		width: 92px;
		transition: outline 0.1s;
		transition: width 0.1s;

		&.focused,
		&:has(:focus) {
			&:hover {
				background-color: inherit;
			}

			cursor: text;
			width: 235px;

			outline: 1px solid $bg-clr1;
			height: 100%;
			border-radius: 4px;
		}
	}

	.filter-search-input {
		all: unset;
		width: 60px;
		width: 100%;
		height: 28px;
		font-size: em(14px);
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		padding-inline-start: 5px;
	}

	& > * {
		display: flex;
		align-items: center;
		.filter-text {
			padding-inline-end: em(12px);
			font-size: em(14px);
		}
	}

	.filter-icon {
		color: $icon-color;
		width: 32px;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	svg {
		font-weight: lighter;
	}
}

.board-header-bottom {
	display: flex;
	padding-block: 16px;
}
