.activity-preview {
	height: rem(56px);
	border-block-end: 1px solid #c4cad9;
	justify-content: space-around;
	display: grid;
	grid-template-columns: rem(40px) rem(160px) rem(112px) 1fr;
	grid-template-rows: 1fr;
	grid-column-gap: 0px;
	grid-row-gap: 0px;
	grid-gap: 3em;

    .user-img {
        min-width: 30px;
        min-height: 30px;
        flex: 0;
        height: 30px;
        width: 30px;
        border-radius: 50%;
    }

    .timesince {
        grid-area: 1 / 1 / 2 / 2;
    }

    .activity-and-user {
        grid-area: 1 / 2 / 2 / 3;
    }

	.title {
		grid-area: 1 / 3 / 2 / 4;
	}

	.action-type {
		grid-area: 1 / 3 / 2 / 5;
		width: fit-content;

		span {
			text-overflow: ellipsis;
			overflow: hidden;
			white-space: nowrap;
		}
	}

    .dynamic-cmp {
        grid-area: 1 / 4 / 2 / 6;
        display: flex;
        justify-content: center;
        align-items: center;

        span {
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
        }

		span {
			text-overflow: ellipsis;
			overflow: hidden;
			white-space: nowrap;
		}

		.old-label,
		.new-label {
			color: $clr0;
			height: rem(39px);
			max-width: rem(85px);
			width: rem(85px);
			text-overflow: ellipsis;
			overflow: hidden;
			white-space: nowrap;
			padding: 8px;
			border-radius: 4px;
		}

		.arrow {
			transform: rotate(180deg);
			width: fit-content;

			svg {
				color: $clr9;
				min-height: 12px;
				min-width: 12px;
				margin: 0;
			}
		}

		.timeline-container {

			.timeline-wrapper {
				overflow: hidden;
				white-space: nowrap;
				max-width: 60px;
				text-overflow: ellipsis;
				color: $clr0;
			}
		}
	}
}
